/**
 * 日志管理类
 * Logger management class
 */
export class Logger {
    constructor(prefix) {
        this.logLevel = 'info';
        this.logCounter = 0;
        this.lastResetTime = Date.now();
        this.prefix = prefix;
    }
    /**
     * 设置日志级别
     * Set log level
     */
    setLogLevel(level) {
        this.logLevel = level;
    }
    /**
     * 输出普通日志
     * Output normal log
     */
    log(message, ...args) {
        this.logMessage('info', console.log, message, ...args);
    }
    /**
     * 输出错误日志
     * Output error log
     */
    error(message, error) {
        if (this.shouldLog('error')) {
            let errorMessage;
            if (error === null || error === undefined) {
                errorMessage = '<no error details>';
            }
            else if (error instanceof Error) {
                errorMessage = `${error.message}\n${error.stack}`;
            }
            else {
                try {
                    errorMessage = JSON.stringify(error);
                }
                catch (e) {
                    errorMessage = String(error);
                }
            }
            console.error(`${this.prefix} ${getTimestamp()}: ${message}:`, errorMessage);
        }
    }
    /**
     * 输出调试日志
     * Output debug log
     */
    debug(message, ...args) {
        this.logMessage('debug', console.debug, message, ...args);
    }
    /**
     * 检查是否应该输出日志
     * Check if should output log
     */
    shouldLog(minLevel) {
        return Logger.LOG_LEVELS.indexOf(this.logLevel) <= Logger.LOG_LEVELS.indexOf(minLevel);
    }
    /**
     * 输出警告日志
     * Output warning log
     */
    warn(message, ...args) {
        this.logMessage('info', console.warn, message, ...args);
    }
    /**
     * 输出信息日志
     * Output info log
     */
    info(message, ...args) {
        this.logMessage('info', console.info, message, ...args);
    }
    /**
     * 统一的日志输出方法
     */
    logMessage(level, consoleMethod, message, ...args) {
        if (this.shouldLog(level) && !this.shouldThrottle()) {
            consoleMethod(`${this.prefix} ${getTimestamp()}: ${message}`, ...args);
        }
    }
    shouldThrottle() {
        const now = Date.now();
        if (now - this.lastResetTime >= Logger.THROTTLE_INTERVAL) {
            this.logCounter = 0;
            this.lastResetTime = now;
            return false;
        }
        this.logCounter++;
        return this.logCounter > Logger.MAX_LOGS_PER_INTERVAL;
    }
}
Logger.LOG_LEVELS = ['debug', 'info', 'error'];
Logger.THROTTLE_INTERVAL = 1000; // 1秒内最多输出的日志数
Logger.MAX_LOGS_PER_INTERVAL = 100;
/**
 * 日志工厂类
 * Logger factory class
 * 管理不同模块的Logger实例
 */
export class LoggerFactory {
    constructor() {
        this.loggers = new Map();
        this.defaultPrefix = 'BBBunny';
        this.SEPARATOR = ':';
    }
    /**
     * 获取LoggerFactory实例
     * Get LoggerFactory instance
     */
    static getInstance() {
        if (!LoggerFactory.instance) {
            LoggerFactory.instance = new LoggerFactory();
        }
        return LoggerFactory.instance;
    }
    /**
     * 设置默认前缀
     * Set default prefix
     */
    setDefaultPrefix(prefix) {
        this.defaultPrefix = prefix;
    }
    /**
     * 获取指定模块的Logger
     * Get Logger for specific module
     * @param module 模块路径，可以是多级，如 ['ContentScript', 'Timer']
     */
    getLogger(module) {
        const modulePath = Array.isArray(module) ? module : [module];
        const loggerKey = modulePath.join(this.SEPARATOR);
        const fullPrefix = `${this.defaultPrefix}${this.SEPARATOR}${loggerKey}`;
        if (!this.loggers.has(loggerKey)) {
            this.loggers.set(loggerKey, new Logger(fullPrefix));
        }
        return this.loggers.get(loggerKey);
    }
    /**
     * 设置全局日志级别
     * Set global log level
     */
    setGlobalLogLevel(level) {
        this.loggers.forEach(logger => logger.setLogLevel(level));
    }
}
function getTimestamp() {
    const now = new Date();
    return `[${now.toLocaleString()}]`;
}
